(function() {
  angular.module('ndAssociatesTest.resources', []);

  angular.module('ndAssociatesTest.controllers', []);

  angular.module('ndAssociatesTest.directives', []);

  angular.module('ndAssociatesTest', ['ndAssociatesTest.resources', 'ndAssociatesTest.controllers', 'ndAssociatesTest.directives', 'ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'LocalStorageModule']);

}).call(this);



(function() {
  (function() {
    var NdAssociatesProductDirective;
    NdAssociatesProductDirective = function() {
      return {
        scope: {
          product: '=?'
        },
        restrict: 'E',
        controller: 'NdAssociatesProductDirectiveController',
        controllerAs: 'vm',
        templateUrl: 'app/components/directives/nd-associates-product/nd-associates-product.template.html',
        bindToController: true
      };
    };
    return angular.module('ndAssociatesTest.directives').directive('ndAssociatesProduct', NdAssociatesProductDirective);
  })();

}).call(this);



(function() {
  (function() {
    var NdAssociatesProductDirectiveController;
    NdAssociatesProductDirectiveController = (function() {
      function NdAssociatesProductDirectiveController($scope, localStorageService) {
        this.$scope = $scope;
        this.localStorageService = localStorageService;
      }

      NdAssociatesProductDirectiveController.prototype.addToCart = function() {
        var count;
        count = parseInt(this.localStorageService.get(this.product.id)) || 0;
        count += 1;
        return this.localStorageService.set(this.product.id, count);
      };

      return NdAssociatesProductDirectiveController;

    })();
    NdAssociatesProductDirectiveController.$inject = ['$scope', 'localStorageService'];
    return angular.module('ndAssociatesTest.controllers').controller('NdAssociatesProductDirectiveController', NdAssociatesProductDirectiveController);
  })();

}).call(this);



(function() {
  (function() {
    var ProductsMenuController;
    ProductsMenuController = (function() {
      function ProductsMenuController($scope, $state, $stateParams, CategoriesResource) {
        this.$scope = $scope;
        this.$state = $state;
        this.$stateParams = $stateParams;
        this.CategoriesResource = CategoriesResource;
        this.categories = this.CategoriesResource.index();
        this.categories.unshift({
          id: 0,
          title: "All"
        });
      }

      ProductsMenuController.prototype.selectCategory = function(id) {
        return this.$scope.$broadcast('tab:change', {
          category_id: id
        });
      };

      return ProductsMenuController;

    })();
    ProductsMenuController.$inject = ['$scope', '$state', '$stateParams', 'CategoriesResource'];
    return angular.module('ndAssociatesTest.controllers').controller('ProductsMenuController', ProductsMenuController);
  })();

}).call(this);



(function() {
  (function() {
    var ProductsIndexController;
    ProductsIndexController = (function() {
      function ProductsIndexController($scope, $state, $stateParams, ProductsResource) {
        this.$scope = $scope;
        this.$state = $state;
        this.$stateParams = $stateParams;
        this.ProductsResource = ProductsResource;
        this.$scope.$on('tab:change', (function(_this) {
          return function(event, data) {
            if (data.category_id === 0) {
              return _this.products = _this.ProductsResource.index();
            } else {
              return _this.products = _this.ProductsResource.index({
                category_id: data.category_id
              });
            }
          };
        })(this));
      }

      return ProductsIndexController;

    })();
    ProductsIndexController.$inject = ['$scope', '$state', '$stateParams', 'ProductsResource'];
    return angular.module('ndAssociatesTest.controllers').controller('ProductsIndexController', ProductsIndexController);
  })();

}).call(this);



